<template>
    <div @click="goVideoList(course)">
        <div>
            <img :src="course.thumb" alt mode>
        </div>
        <div>
            <div>
                {{course.title}}
                <div v-html="course.description"></div>
            </div>
            <div v-if="course.charge">{{course.charge}}</div>
            <div v-else>免费课程</div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        course: {
            type: Object,
            default: () => {
            }
        }
    },
    methods: {
        goVideoList() {
            this.$emit("goVideoList", this.course)
        }
    }

}
</script>