<template>
    <div class="index">
        <h1>首页</h1>
        <h1>你好</h1>
        <!--        <p>尊敬的{{this.$store.getters.memberInfo}}，欢迎来到这里。</p>-->
        <p>尊敬的{{memberInfo}}，欢迎来到这里。</p>
        <div>
            <card v-for="(item,index) in courseList" :key="index" :course="item" @goVideoList="goVideoList"></card>
        </div>
        <button @click="recharge">充值</button>
    </div>
</template>

<script>
import card from "../components/card";
import {mapGetters, mapState} from 'vuex'


export default {
    components: {
        card
    },
    data() {
        return {
            courseList: []
        }
    },
    created() {
        // Mock
        this.courseList = [
            {
                id: "1",
                thumb:
                    "//img1.mukewang.com/5cb831fd0949d9f306000338-590-330.jpg",
                title: "学习vuex",
                description: "2312",
                charge: "",
                userStatus: 0,
                vipLevel: 0
            },
            {
                id: "2",
                thumb:
                    "//img1.mukewang.com/5cb831fd0949d9f306000338-590-330.jpg",
                title: "实战课程",
                description: "2312",
                charge: "实战课程",
                userStatus: 1,
                vipLevel: 0
            },
            {
                id: "3",
                thumb:
                    "//img1.mukewang.com/5cb831fd0949d9f306000338-590-330.jpg",
                title: "v12会员专享课程",
                description: "2312",
                charge: "v12会员专享",
                userStatus: 2,
                vipLevel: 12
            }
        ];
    },
    computed: {
        ...mapState(["userStatus", "vipLevel"]),
        ...mapGetters(["memberInfo"])
    },
    mounted() {
        console.log(this.userStatus, this.vipLevel, "---->")
    },
    methods: {
        recharge() {
            this.$router.push("./userCenter")
        },
        goVideoList(e) {
            const res = this.checkPermission(e)
            if (res) {
                this.$router.push({
                    name: "course",
                    params: {
                        id: e.id
                    }
                });
            } else {
                alert("权限不足，无法观看。")
            }
        },
        checkPermission(e) {
            const userStatus = this.$store.state.userStatus;
            const vipLevel = this.$store.state.vipLevel;
            if (userStatus >= e.userStatus) {
                if (vipLevel >= e.vipLevel) {
                    return true;
                }
            }
            return false
        }
    }
}
</script>